* {
  font-family: trebuchet ms, verdana, helvetica, arial, sans-serif;
}

html, body {
  margin: 0;
  padding: 0;
}

.main {
  height: 100vh;
  background-color: #2e3440;
  flex-direction: column;
  display: flex;
  position: relative;
}

.main ul {
  border-top: 1px solid #ffffff1a;
  flex-wrap: wrap;
  margin: 0;
  padding: 16px;
  list-style: none;
  display: flex;
}

.main ul li {
  cursor: pointer;
  background-color: #ffffff1a;
  border-radius: 4px;
  flex: 1;
  justify-content: center;
  align-items: center;
  margin: 4px;
  padding: 8px 16px;
  transition: opacity .3s;
  display: flex;
}

.main ul li span {
  color: #fff;
  font-size: 2vw;
}

.main ul li:hover {
  opacity: .5;
}

.timer-wrapper {
  height: 100%;
  justify-content: center;
  align-items: center;
  display: flex;
}

.timer-wrapper span {
  text-align: center;
  color: #fff;
  cursor: pointer;
  font-size: 17vw;
  transition: opacity .3s;
}

.timer-wrapper span:hover {
  opacity: .3;
}

#background-timer {
  z-index: 1;
  pointer-events: none;
  background-color: #ffffff1a;
  transition: bottom .3s;
  position: absolute;
  inset: 0;
}

.gh-link {
  width: 30px;
  position: absolute;
  top: 10px;
  right: 10px;
}

.gh-link img {
  width: 100%;
  object-position: center;
}

